import React from 'react'
import PropTypes from 'prop-types'
import { platform, version } from '../../lib'
import MapNoMatch from './MapNoMatch'
import MapArcgis4 from './MapArcgis4'

function getMatch() {
    const esriNS = window.require('esri/kernel')
    window.require(['esri/kernel'], (n) => {
        console.log(n.version)
    })
    if (platform === 'arcgis' && version[0] && version[0] === '4') {
        return MapArcgis4
    }
    return MapNoMatch
}

class Map extends React.Component {
    static propTypes = {
        children: PropTypes.array,
    }
    static defaultProps = {}

    constructor() {
        super()
        this.mapClass = getMatch()
    }

    render() {
        const MapClass = this.mapClass
        return (<MapClass {...this.props}/>)
    }
}
export default Map



