const { platform, version } = (() => {
    if (window.require) {
        // const esriNS = window.require('esri/kernel')
        return { platform: 'arcgis', version: '4.4' }// esriNS.version }
    }
    return { platform: 'unknown', version: '' }
})()
export { platform, version }



